.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 25px;
  padding: 5px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  margin: 0;
}

.App-intro {
  font-size: large;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.menu-item {
  width: 250px;
  background-color: steelblue;
  padding: 10px;
  margin: 10px;
  border-radius: 3px;
  display: flex;
}
.menu-item a {
  color: white;
  margin: auto;
}

.App-textarea {
  height: 200px;
  width: 500px;
}
